.show-favorite {
  position: relative;
}
.icon-heart-empty {
  position: absolute;
  top: 5px;
  right: 5px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.icon-heart {
  position: absolute;
  top: 5px;
  right: 5px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}
.show-favorite .icon-heart {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.show-favorite .icon-heart-empty {
  -webkit-transform: scale(0);
          transform: scale(0);
}

.speaker-detail img {
  max-width: 140px;
  border-radius: 50%;
}
.speaker-detail p {
  color: #60646B;
}

.about-header {
  background-color: #434954;
  padding: 16px;
  width: 100%;
  min-height: 150px;
  text-align: center;
}
.about-header img {
  max-width: 200px;
  min-height: 115px;
  margin-left: -15px;
  padding: 25px 0 20px 0;
}
.about-info p {
  color: #697072;
  text-align: left;
}
.about-info ion-icon {
  width: 20px;
}
.md .about-info [text-right],
.wp .about-info [text-right] {
  margin-right: 0;
}
.ios .about-info {
  text-align: center;
}
.ios .about-info ion-icon {
  width: auto;
  margin-right: 10px;
}

